.navItem {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    margin-left: 32px;
    color: rgba(36, 36, 36, 0.87);
    cursor: pointer;
}
.navItem:hover {
    text-decoration:none;
    color: #FF5800;
}
.navbar-light .navbar-nav .nav-link {
    color: black;
}
.headerItem {
    color: black;
}
.headerPhone a{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: rgba(36, 36, 36, 0.87);
}
.headerPhone a:hover {
    border:none; 
    color: #FF5800;
    text-decoration : none;
}
#container {
    background-image: url('../../assets/Mask Group (1).png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position:  -150px 30px;
}
@media screen and (max-width: 1080px) {
    #container {
        background-position: -240px 30px;
    }
}
@media screen and (max-width: 992px){ 
    #container {
        background-image: none;
    }
    .navItem {
        display: block;
        margin-left: 0px;
        margin-top: 5px;
    }
}