.capabilitiesBoxItem {
    background-color: white;
    padding: 25px; 
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    width: 100%;
    margin-bottom: 20px;
}

.capabilitiesBox {
    padding: 100px 0px;
    display: flex;
    /* justify-content: space-between; */
}
.box {
    display: flex;
    justify-content: flex-start;
}
.boxFirst {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 64px;
    line-height: 56px;
    color: rgba(36, 36, 36, 0.87);
}
.boxSecond {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 40px;
    color: rgba(36, 36, 36, 0.87);
    margin-left: 4px;
}
.boxText {
    padding-top: 16px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: rgba(36, 36, 36, 0.87);
}