.productContainer {
    display: flex;
    flex-direction: column;
}
#newLineFirst,#newLineSecond {
    display: none;
}

.productCaption {
    font-style: normal;
    font-family: "Montserrat",sans-serif;
    font-weight: 800;
    font-size: 43px;
    color: #3F3F3F;
    margin: 0px;
    padding-top: 150px;
}
.productText {
    margin-bottom: 0px;
    margin-top: 20px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: rgba(36, 36, 36, 0.64);
}
.productBtn {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
    color: #FFFFFF;
    padding: 14px 36px;
    background: linear-gradient(360deg, #FF5D02 46.88%, #FF8947 100%, #FF6F1F 100%);
    box-shadow: 0px 8px 24px rgba(255, 93, 2, 0.4);
    border-radius: 100px;
    border:none;
    cursor: pointer;
}
.productBtn:hover {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 32px;
    color: #FFFFFF;
    padding: 14px 36px;
    background: linear-gradient(360deg, #FF5D02 46.88%, #FF8947 100%, #FF6F1F 100%);
    box-shadow: 0px 8px 24px rgba(255, 93, 2, 0.4);
    border-radius: 100px;
    border:none;
    text-decoration: none;
}
.btnBox {
    padding-top: 40px;
    padding-bottom: 200px;
}
#productBtn {
    outline: none;
}
