.lastAdvantageBox {
    padding-top: 44px;
    display: flex;
    justify-content: space-between;
}
.lastAdvantageBoxItem {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
}
.lastAdvantageCaption {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    color: rgba(36, 36, 36, 0.87);
    padding-top: 80px;
    
}
.lastAdvantageContent {
    padding-top: 32px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: rgba(36, 36, 36, 0.87);
    text-align: center;
    padding: 32px 60px 0px 60px;
}
.lastAdvantageText {
    padding: 12px 70px 72px 70px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: rgba(36, 36, 36, 0.64);
}

@media screen and (max-width: 767px) {
    .lastAdvantageText {
        padding: 12px 40px 40px 40px;
    }
    .lastAdvantageCaption {
        padding-top: 50px;
    }
}