.containerAutorization {
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 500px;
    padding: 40px 57px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
}
.autorization {
    padding-top: 100px;
    padding-bottom: 150px;
    background-color: #F5F5F5;
    display: flex;
}
.autoInp {
    width: 100%;
    padding: 22px;
    background: rgba(36, 36, 36, 0.02);
    border: 1px solid rgba(36, 36, 36, 0.12);
    box-sizing: border-box;
    border-radius: 8px;
    margin-bottom: 12px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: rgba(36, 36, 36, 0.64);


}
.autoInp:focus {
    outline: none;
}
.inpBox {
    padding-top:40px;
    width: 100%;
}
.lahzaBtn {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: #FFFFFF;
    padding: 16px 36px;
    background: #FF5800;
    border-radius: 30px;
    border: none; 
    outline: none;
    margin-top: 28px;
    width: 100%;
} 
.lahzaText {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    color: rgba(36, 36, 36, 0.87);
    margin-bottom: 40px;
}
.phoneBox,.nameBox {
    position: relative;
    transition: 0.3s;
}
.phoneBox label,.nameBox label {
    transition: 0.2s;
    cursor: text;
    z-index: 2;
}
.phoneBox input {
    z-index: 1;
}
.phoneLabel,.nameLabel {
    position: absolute;
    top: 20px;
    left: 22px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: rgba(36, 36, 36, 0.64);
}
.phoneLabel-active,.nameLabel-active {
    position: absolute;
    top: 5px;
    left: 22px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 24px;
    color: rgba(36, 36, 36, 0.64);
}
.checkBoxInp {
    width: 30px;
    height: 30px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    border: 10px solid #FF6C2F;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 20px;
    margin-right: 12px;
}
.idCheckbox{
    display: flex;
}
.idCheckbox label{
    font-family: Montserrat;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 20px;
color: rgba(36, 36, 36, 0.64);
}
#lahzaBtn {
    outline: none;
}
@media screen and (max-width: 520px) {
    .containerAutorization {
        width: 470px;
        padding: 30px 40px;
    }
}
@media screen and (max-width: 500px) {
    .containerAutorization {
        width: 440px;
    }
}
@media screen and (max-width: 470px) {
    .containerAutorization {
        width: 370px;
    }
}
@media screen and (max-width: 412px) {
    .containerAutorization {    
        width: 350px;
        padding: 25px 30px;
    }
}
@media screen and (max-width: 370px) {
    .containerAutorization {
        width: 330px;
    }
    .phoneLabel,.nameLabel {
        font-size: 12px;
        top: 10px;
        left: 10px;
    }
    .phoneLabel-active,.nameLabel-active {
        top: 0px;
        left: 10px;
        font-size: 8px;
    }
    .autoInp {
        padding: 11px;
        font-size: 12px;
    }
    .lahzaBtn {
        font-size: 15px;
        padding: 12px 32px;
    }
}
@media screen and (max-width: 340px) {
    .containerAutorization {
        width: 300px;
    }
}