.advantageBox {
    padding-bottom: 64px;
}
.advantageCaption {
    padding-top: 64px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 40px;
    color: rgba(36, 36, 36, 0.87);
    text-align: center;
}
.advantageImg {
    width: 80px;
    height: 80px;
    background: #FF5D02;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    border-radius: 50%;
    line-height: 48px;
}
.advantageContainer {
    padding-top: 50px;
}
.advantageText {
    margin-top: 24px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: rgba(36, 36, 36, 0.87);
}
.advantageLastText {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: rgba(36, 36, 36, 0.64);
    text-align: center;
}