.scrollHeader {
    position: fixed;
    top: -500px;
    left: 0px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    transition: 0.3s ease-in-out;
    width: 100%;
}
.scrollHeader-active {
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
    transition: 0.3s ease-in-out;
    z-index: 1100;
    background: white;
}

