.guestionsContainer {
    border-top: 2px solid #E6E6E6;
    background-color: rgb(245,245,245);
    padding-bottom: 100px;
}
.guestionsCaption {
    padding-bottom: 48px;
    padding-top: 100px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 28px;
    line-height: 32px;
    color: rgba(36, 36, 36, 0.87);
    text-align: center;
}
#questionsBtn {
    outline: none;
}
.questionsBtn {
    margin-top: 48px;
    background:#FF5D02;
    border-radius: 24px;
    color: white;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    padding: 12px 42px;
    font-family: Montserrat;
    border:none;
    outline: none;
}



@media screen and (max-width: 600px) {
    #guestionsContainer {
        padding-left: 0px;
        padding-right: 0px;
        padding-bottom: 50px;
    }
    .guestionsCaption {
        padding-top: 50px;
    }
}